<template>
  <div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="list in bannerList" :style="{backgroundImage: 'url(' +list.FILE_PATH+ ')'}"  @click="changeBanner(list)">
        </div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>

</template>

<script>
  import { jwtServer } from '../../assets/js/jwtServers'
  import Swiper from '../../../static/idangerous.swiper'
  export default {
    data() {
      return {
        bannerList:null
      }
    },
    cumputed: {

    },
    mounted () {
      this.loopBanner()
    },
    methods:{
      loopBanner () {
        let that =this;
        jwtServer.globalPostData('/apis/msgcenter/query/1.0/getBanner/1.0',jwtServer.submitData({"FILE_TYPE": 'huoqiapp_banner'})).then((data)=> {
          this.bannerList =data.result.main_data.data;
          console.log(data);
        })
        var swiper = new Swiper('.swiper-container', {
          loop: true,
          autoplay: 3000,
          paginationClickable :true,
          preventClicks : false,
          nextButton: '.swiper-button-next',
          prevButton: '.swiper-button-prev',
          noSwiping : true,
          effect : false,
          autoplayDisableOnInteraction : false,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          pagination: {
            el: '.swiper-pagination',
            type: 'progressbar'
          },
          onSlideChangeEnd : function(swiper){
            swiper.update();
            swiper.startAutoplay();
            swiper.reLoop();
          }
        });
      },
      changeBanner (list) {
        window.open(list.LINK_COMMENT)
      }
    }
  }
</script>

<style lang="less" scoped>
  @import '../../assets/css/idangerous.swiper.css';
  .swiper-slide{
    -moz-background-size:100% 100%;
    background-size:100% 100%;
    background-repeat: no-repeat;
    width: 7.5rem;
    height: 3.45rem;
  }

</style>



