<template>
  <div class="content" v-if="moneyList !=null">
    <p>已为<span>{{moneyList.CUST_SUM | farmatDataWan}}</span>亿用户撮合融资<span>{{moneyList.INVEST_SUM | farmatDataYi}}</span>亿元，赚取<span>{{moneyList.INCOME_SUM | farmatDataWan}}</span>万元收益</p>
  </div>

</template>

<script>
  import { jwtServer } from '../../assets/js/jwtServers'
  import '../../assets/js/filter'

  export default {
    data () {
      return {
        moneyList:null
      }
    },
    mounted () {
      this.getMoney()
    },
    methods: {
      getMoney () {
        jwtServer.globalPostData('/apis/msgcenter/query/1.0/queryMainPageTotalData/1.0',jwtServer.submitData({})).then((data)=> {
          this.moneyList =data.result.main_data.data[0];
          console.log(this.moneyList);
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  @bg:#fff;
  .content{
    width: 100%;
    height: 100%;
    >p{
      width: 7.5rem;
      height:0.7rem ;
      background:@bg;
      font-size: 0.26rem;
      color: #666;
      line-height: 0.7rem;
      text-align: center;
      span{
        color:#fb4747 ;
      }
    }
  }
</style>



